import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function grossProfitMarginTtm(tableName: string) {
  const title = "Gross Margin (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["grossProfitMarginTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Profit after production costs.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about gross margin?`,
              value: info.row.original.financialData.companyRatios?.data?.grossProfitMarginTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyRatios?.data?.grossProfitMarginTtm ?? 0) * 100
          }
        />
      )
    },
  })
}

export { grossProfitMarginTtm }
